@charset "UTF-8";
@import-normalize;
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body,
blockquote, figure,
form,
h1, h2, h3, h4, h5, h6,
ol, ul,
p, pre {
  margin: 0;
}

body {
  font-size: 1.6rem;
}

img {
  max-width: 100%;
}

ul {
  padding-left: 0;
}

button {
  border: 0;
  background: unset;
  color: currentColor;
  cursor: pointer;
}

:root {
  --primary: rgb(235, 235, 235);
  --secondary: rgb(55, 66, 88);
  --accent: rgb(173, 185, 201);
  --trans: all .4s ease-in-out;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  background-color: #f3e6e8;
  background-image: linear-gradient(315deg, #f3e6e8 0%, #d5d0e5 74%);
  font-family: "Source Sans Pro", sans-serif;
}

a {
  text-decoration: none;
}

a, button {
  transition: var(--trans);
}

h1, h2, h3, nav {
  font-family: "Libre Baskerville", serif;
}

h2 {
  font-size: 2.5em;
}

ul {
  list-style: none;
}

main {
  padding: 0 2.5%;
}

section {
  min-height: 100vh;
  padding: 1em 0;
}
section > h2 {
  color: var(--secondary);
  font-weight: 700;
}

.btn {
  max-width: 80%;
  margin: 0 auto;
  padding: 0.5em 2em 0.5em 2.5em;
  border: 1px solid var(--primary);
  color: var(--primary);
  font-family: "Libre Baskerville", serif;
  transition: var(--trans);
}
@media (hover: hover) and (pointer: fine) {
  .btn:hover, .btn:focus {
    transition: var(--trans);
    border: 1px solid var(--secondary);
    background-color: var(--secondary);
  }
}
@media (hover: none) {
  .btn:active, .btn:focus {
    transition: var(--trans);
    border: 1px solid var(--secondary);
    background-color: var(--secondary);
    transform: scale(1.1);
  }
}

#work, #about article, #about div, #contact, #contact form {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
  place-content: center space-between;
}

#svg-sprite {
  display: none;
}

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 50%;
  height: 0;
  padding-top: 50%;
  position: relative;
}
.icon svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 1em;
  place-content: center;
  background-image: linear-gradient(147deg, #000000 0%, #2c3e50 74%);
  height: 100vh;
  color: var(--primary);
  text-align: center;
  text-transform: uppercase;
}
header .btn:after {
  display: inline-block;
  content: "→";
  margin-left: 1em;
  transition: all 0.4s ease-in-out;
}
header .btn:hover:after {
  transform: rotate(90deg);
}

footer {
  width: 100%;
  padding: 1em 2.5%;
  background-color: var(--secondary);
  text-align: center;
  color: var(--primary);
  font-size: 2rem;
}
footer a {
  color: var(--accent);
}
footer a:hover, footer a:focus {
  color: var(--primary);
}

nav {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1;
  overflow: hidden;
  padding: 1em;
}
nav button {
  position: relative;
  z-index: 2;
  width: 5rem;
  height: 5rem;
  display: none;
  background: var(--secondary);
  border: 0;
  border-radius: 5px;
  box-shadow: 0px 0px 5px black, 2px 2px 5px black, 0px 0px 8px black inset;
  color: var(--primary);
  cursor: pointer;
  font-size: 1.5em;
  text-align: center;
}
nav button:focus, nav button:hover {
  color: var(--accent);
}
nav button:after {
  content: "☰";
  display: block;
  margin-top: -2px;
}
nav button.active:after {
  content: "X";
}
nav button span {
  /* Only show if can't load stylesheets */
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  overflow: hidden;
}
nav a {
  color: var(--primary);
  font-size: 1.2em;
}
nav a:hover, nav a:focus {
  color: var(--secondary);
}

.js nav button {
  display: block;
}
.js nav div {
  position: fixed;
  left: 0;
  top: 0;
  display: none;
  height: 100vh;
  width: 100%;
  background: black;
}
.js nav div.active {
  display: block;
}
.js nav ul {
  display: flex;
  flex-direction: column;
  place-content: center;
  gap: 1em;
  width: 100%;
  height: 100vh;
  text-align: center;
}

#work {
  place-content: center;
}
#work h2 {
  flex-basis: 100%;
  margin: 1em 0;
}
#work h3 {
  font-size: 2rem;
  color: var(--secondary);
  text-transform: uppercase;
}
#work article {
  min-width: 18rem;
  flex-basis: 30%;
  text-align: center;
}
#work p {
  padding: 1em;
}
#work .icon {
  margin: 1em auto;
  color: var(--secondary);
}

/* PROFILE */
#about article {
  gap: 0;
  margin-top: 2em;
  border: 1px solid var(--secondary);
  box-shadow: 0px 2px 8px 1px #000;
}
#about div {
  flex: 1;
  flex-direction: column;
  min-width: 35rem;
  padding: 2em;
  background: var(--accent);
  text-align: center;
}
#about div img {
  max-width: 20rem;
  margin: 0 auto;
  border-radius: 100%;
}
#about div h3 {
  font-size: 2.5rem;
}
#about div p {
  font-size: 2rem;
}
#about div ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
#about div li {
  padding: 0.5em 1em;
  margin: 0.5em;
  border: 1px solid var(--secondary);
  border-radius: 0.5rem;
  box-shadow: 1px 1px 5px 1px var(--secondary);
  background: var(--secondary);
  transition: var(--trans);
}
@media (hover: hover) and (pointer: fine) {
  #about div li:hover, #about div li:focus {
    transition: var(--trans);
    opacity: 0.8;
  }
}
#about div li a {
  color: var(--primary);
}
#about aside {
  min-width: 35rem;
  padding: 2em;
  flex: 1;
  background: var(--secondary);
  color: var(--primary);
}
#about aside li {
  position: relative;
  margin: 20px 0;
}
#about aside li > span {
  display: block;
  height: 4px;
  background-color: #353b48;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  box-shadow: 0px 0px 10px #2187e7b3;
  overflow: hidden;
}
#about aside li span span {
  height: 4px;
  float: left;
  background: #2187e7;
}
#about aside li span span:after {
  position: absolute;
  top: 0;
  right: 1%;
  font-size: 1.5rem;
  display: block;
}
#about aside .html {
  animation: uc2n670 2s forwards;
}
@keyframes uc2n670 {
  from {
    width: 0%;
  }
  to {
    width: 90%;
  }
}
#about aside .html:after {
  content: "90%";
}
#about aside .css {
  animation: uc2n671 2s forwards;
}
@keyframes uc2n671 {
  from {
    width: 0%;
  }
  to {
    width: 90%;
  }
}
#about aside .css:after {
  content: "90%";
}
#about aside .javascript {
  animation: uc2n67a 2s forwards;
}
@keyframes uc2n67a {
  from {
    width: 0%;
  }
  to {
    width: 60%;
  }
}
#about aside .javascript:after {
  content: "60%";
}
#about aside .jquery {
  animation: uc2n67s 2s forwards;
}
@keyframes uc2n67s {
  from {
    width: 0%;
  }
  to {
    width: 45%;
  }
}
#about aside .jquery:after {
  content: "45%";
}
#about aside .nodejs {
  animation: uc2n67t 2s forwards;
}
@keyframes uc2n67t {
  from {
    width: 0%;
  }
  to {
    width: 30%;
  }
}
#about aside .nodejs:after {
  content: "30%";
}
#about aside .php {
  animation: uc2n68i 2s forwards;
}
@keyframes uc2n68i {
  from {
    width: 0%;
  }
  to {
    width: 65%;
  }
}
#about aside .php:after {
  content: "65%";
}
#about aside .git {
  animation: uc2n68t 2s forwards;
}
@keyframes uc2n68t {
  from {
    width: 0%;
  }
  to {
    width: 70%;
  }
}
#about aside .git:after {
  content: "70%";
}
#about aside .wordpress {
  animation: uc2n69b 2s forwards;
}
@keyframes uc2n69b {
  from {
    width: 0%;
  }
  to {
    width: 75%;
  }
}
#about aside .wordpress:after {
  content: "75%";
}
#about aside .joomla {
  animation: uc2n69y 2s forwards;
}
@keyframes uc2n69y {
  from {
    width: 0%;
  }
  to {
    width: 40%;
  }
}
#about aside .joomla:after {
  content: "40%";
}
#about aside .gimp {
  animation: uc2n6a8 2s forwards;
}
@keyframes uc2n6a8 {
  from {
    width: 0%;
  }
  to {
    width: 45%;
  }
}
#about aside .gimp:after {
  content: "45%";
}
#about aside .inkscape {
  animation: uc2n6am 2s forwards;
}
@keyframes uc2n6am {
  from {
    width: 0%;
  }
  to {
    width: 80%;
  }
}
#about aside .inkscape:after {
  content: "80%";
}

#projects img {
  width: 100%;
}
#projects div {
  width: 100%;
  padding: 2em;
  display: grid;
  grid-template-columns: repeat(auto-fit, 25rem);
  place-content: center;
  gap: 1em;
  color: var(--primary);
}
#projects article, #projects figure {
  position: relative;
}
#projects figcaption {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0.2em;
  background: var(--secondary);
  font-size: 1.4em;
  text-align: center;
}
#projects article div {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 1em;
  place-content: center;
  background: rgba(25, 51, 77, 0.8);
  color: antiquewhite;
  opacity: 0;
  text-align: center;
  transition: var(--trans);
}
@media (hover: hover) and (pointer: fine) {
  #projects article div:hover, #projects article div:focus {
    transition: var(--trans);
    opacity: 1;
  }
}

/* Contact */
#contact > h2 {
  flex-basis: 100%;
}
#contact address {
  flex: 1;
  align-self: center;
  justify-self: center;
  font-size: 2em;
}
@media (max-width: 960px) {
  #contact address {
    order: 3;
    text-align: center;
  }
}
#contact .adr span {
  display: block;
}
@media (max-width: 960px) {
  #contact .adr span {
    display: inline-block;
  }
}
#contact form {
  place-content: center;
  max-width: 72rem;
  margin: 5rem auto;
  padding: 3rem;
  background: #161616;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 5px black, 2px 2px 5px black, 0px 0px 8px black inset;
}
#contact form div {
  flex: 1;
  flex-basis: 100%;
  position: relative;
  margin: 1rem 0;
}
#contact form .small-input {
  flex: 1 1 45%;
  min-width: 20rem;
}
#contact form input, #contact form textarea {
  padding: 2rem;
  background-color: #2d2d2d;
  border: 0;
  border-radius: 0.5rem;
  color: white;
  font-size: 1rem;
  width: 100%;
}
#contact form input:focus, #contact form textarea:focus {
  outline: 0;
}
#contact form input:focus ~ label, #contact form textarea:focus ~ label {
  font-size: 0.75em;
  color: #999;
  top: -5px;
  transition: all 0.225s ease;
}
#contact form label {
  color: #999;
  padding: 1rem 1.5rem;
  position: absolute;
  top: 0.5rem;
  left: 0;
  transition: all 0.25s ease;
}
#contact form textarea {
  min-height: 15em;
}
#contact form .btn {
  background: var(--secondary);
  color: var(--accent);
}
@media (hover: hover) and (pointer: fine) {
  #contact form .btn:hover, #contact form .btn:focus {
    transition: var(--trans);
    background: var(--accent);
    color: var(--secondary);
  }
}